<template>
    <div>
        <b-modal class="downloadModal" id="downloadMp3" centered hide-footer title="Select Quality">
            <a class="btn btn-primary btn-block" @click="hideModal('downloadMp3')" :href="getMp3(downloadData.link, '320')">MP3 320</a>
            <a class="btn btn-primary btn-block" @click="hideModal('downloadMp3')" :href="downloadData.link">MP3 256</a>
            <a class="btn btn-primary btn-block" @click="hideModal('downloadMp3')" :href="getMp3(downloadData.link, '192')">MP3 192</a>
            <a class="btn btn-primary btn-block" @click="hideModal('downloadMp3')" :href="downloadData.hq_link">M4A High</a>
            <a class="btn btn-primary btn-block" @click="hideModal('downloadMp3')" :href="downloadData.lq_link">M4A Low</a>
            <a class="btn btn-primary btn-block" @click="hideModal('downloadMp3')" :href="downloadData.hq_hls">M3U8 High</a>
            <a class="btn btn-primary btn-block" @click="hideModal('downloadMp3')" :href="downloadData.lq_hls">M3U8 Low</a>
        </b-modal>
        <b-modal class="downloadModal" id="downloadPodcast" centered hide-footer title="Select Quality">
            <a class="btn btn-primary btn-block" @click="hideModal('downloadPodcast')" :href="getPodcast(downloadData.link, '256')">MP3 256</a>
            <a class="btn btn-primary btn-block" @click="hideModal('downloadPodcast')" :href="downloadData.link">MP3 192</a>
            <a class="btn btn-primary btn-block" @click="hideModal('downloadPodcast')" :href="downloadData.hq_link">M4A High</a>
            <a class="btn btn-primary btn-block" @click="hideModal('downloadPodcast')" :href="downloadData.lq_link">M4A Low</a>
            <a class="btn btn-primary btn-block" @click="hideModal('downloadPodcast')" :href="downloadData.hq_hls">M3U8 High</a>
            <a class="btn btn-primary btn-block" @click="hideModal('downloadPodcast')" :href="downloadData.lq_hls">M3U8 Low</a>
        </b-modal>
        <b-modal class="downloadModal" id="downloadVideo" centered hide-footer title="Select Quality">
            <a class="btn btn-primary btn-block" @click="hideModal('downloadVideo')" :href="downloadData.high_web">MP4 High</a>
            <a class="btn btn-primary btn-block" @click="hideModal('downloadVideo')" :href="downloadData.low_web">MP4 Low</a>
        </b-modal>
    </div>
</template>

<script>
import { mapState } from 'vuex';
export default {
    name: "Modal",
    computed: {
        ...mapState('modal',[
            'downloadType',
            'downloadData'
        ]),
    },
    methods: {
        getMp3(link,quality) {
            if (link) {
                return link.replace('mp3/mp3-256', 'mp3/mp3-'+quality);
            }
        },
        getPodcast(link,quality) {
            if (link) {
                return link.replace('podcast/mp3-192', 'podcast/mp3-'+quality);
            }
        },
        hideModal (modalId) {
            this.$root.$emit('bv::hide::modal', modalId);
        },
    }
}
</script>