<template>
    <div v-if="tag === 'mp3'">
        <button v-b-modal="'downloadMp3'" @click="boxClicked('mp3', data)" class="rjd_box">
            <img v-lazy="img" :alt="song">
            <span class="tag">MP3</span>
            <div class="song_info">
                <span class="artist_name" v-text="artist" v-if="artist"></span>
                <span class="song_name" v-text="song" v-if="song"></span>
            </div>
        </button>
    </div>
    <div v-else-if="tag === 'podcast'">
        <button v-b-modal="'downloadPodcast'" @click="boxClicked('podcast', data)" class="rjd_box">
            <img v-lazy="img" :alt="song">
            <span class="tag">podcast</span>
            <div class="song_info">
                <span class="artist_name" v-text="artist" v-if="artist"></span>
                <span class="song_name" v-text="song" v-if="song"></span>
            </div>
        </button>
    </div>
    <div v-else-if="tag === 'video'">
        <button v-b-modal="'downloadVideo'" @click="boxClicked('video', data)" class="rjd_box">
            <img v-lazy="img" :alt="song">
            <span class="tag">video</span>
            <div class="song_info">
                <span class="artist_name" v-text="artist" v-if="artist"></span>
                <span class="song_name" v-text="song" v-if="song"></span>
            </div>
        </button>
    </div>
    <div v-else-if="tag === 'playlist'">
        <router-link :to="'/playlist/'+data" class="rjd_box">
            <img v-lazy="img" :alt="artist">
            <div class="song_info">
                <span class="artist_name" v-text="artist" v-if="artist"></span>
            </div>
        </router-link>
    </div>
    <div v-else-if="tag === 'mp3_album'">
        <router-link :to="'/album/'+data.id" class="rjd_box">
            <img v-lazy="img" :alt="song">
            <span class="tag">Album</span>
            <div class="song_info">
                <span class="artist_name" v-text="artist" v-if="artist"></span>
                <span class="song_name" v-text="song" v-if="song"></span>
            </div>
        </router-link>
    </div>
</template>

<script>
import { mapState,mapMutations } from 'vuex';

export default {
    name: 'rjdbox',
    props: ['img','tag','artist','song','data'],
    computed: {
        ...mapState('modal',[
            'downloadType',
            'downloadData'
        ]),
    },
    methods: {
        boxClicked(type,data) {
            this.setDownloadType(type);
            this.setDownloadData(data);
        },
        ...mapMutations('modal',[
            'setDownloadType',
            'setDownloadData'
        ]),
    }
}
</script>